import React from 'react';
import logo from './logo.png';

class Table extends React.Component {
    constructor(props) {
        super(props);
        this.state = {total_num_creditos: 0,total_nota_creditos: 0}
        //this.state = { }; //Para los tabs
      }
    aproximarDecimales(num){
        
        return +(Math.round(num + "e+2")  + "e-2");
    }

    

    render() {
      const items = this.props.items;
      
      var total_num_creditos = this.props.items.reduce(function(prev, cur) {
        return prev + cur.creditos;
      }, 0);

      var total_nota_creditos = this.props.items.reduce(function(prev, cur) {
        return prev + cur.nota_creditos;
      }, 0);
      

      return (
        <div className="card" id="Table">
          <table className="table table-bordered table-responsive">
            <tbody>
              <tr>
                <th>Cursos</th>
                <th>Corte 1</th>
                <th>Corte 1(%)</th>
                <th>Corte 2</th>
                <th>Corte 2(%)</th>
                <th>Corte 3</th>
                <th>Corte 3(%)</th>
                <th>Créditos</th>
                <th>Nota del curso</th>
                <th>Nota x Créditos</th>
              </tr>
              {items.map(item => {

                  
                  //console.log(this.state.total_num_creditos);
                  console.log('Ok :'+total_num_creditos);
                return (
                  <tr key={item.key}>
                    <td>{item.materia}</td>
                    <td>{item.nota1}</td>
                    <td>{this.aproximarDecimales(item.nota1p)}</td>
                    <td>{item.nota2}</td>
                    <td>{this.aproximarDecimales(item.nota2p)}</td>
                    <td>{item.nota3}</td>
                    <td>{this.aproximarDecimales(item.nota3p)}</td>
                    <td>{item.creditos}</td>
                    <td>{this.aproximarDecimales(item.nota_curso)}</td>
                    <td>{this.aproximarDecimales(item.nota_creditos)}</td>
                  </tr>
                );
              })}
            </tbody>
            <tfoot>
                    
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th>{total_num_creditos}</th>
                    <th></th>
                    <th>{this.aproximarDecimales(total_nota_creditos)}</th>
                  </tr>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th>PPP</th>
                    <th>{this.aproximarDecimales(total_nota_creditos/total_num_creditos)}</th>
                  </tr>
                </tfoot>
          </table>
        </div>
      );
    }
  }

export default Table;