import React from 'react';
import logo from './logo.png';

class CalculaPPS extends React.Component {
  constructor(props) {
    super(props);
    this.state = {nota4: 0}
  }
  /*
    CC : Creditos intentados
    PC : Puntos de calidad PGA totales
    CS : Creditos Matriculados en el semestre
  */
  CCRef = React.createRef();
  PCRef = React.createRef();
  CSRef = React.createRef();  

  calcularPPS(e) {
    e.preventDefault();    

    let CC = parseInt(this.CCRef.current.value);
    let PC = parseInt(this.PCRef.current.value);
    let CS = parseInt(this.CSRef.current.value);
    
    this.setState({
      nota4: (3.2*(CC+CS)-(CC*(PC/CC)))/CS
    });

    e.target.reset();
    return this.state.nota4;
  }

  mostrarResultado = () => {

    if (this.state.nota4 === 0) return null;

    return (
      <h3 className="text-center alert alert-info">
        PPS requerido para superar PA y RA:<br/><h2><strong className="">{this.state.nota4}</strong></h2>
      </h3>
    )
  }

  render() {
    return (
            <div>
            <h2 className="text-center">Cuanto PPS se debe obtener para superar PA(Prueba Académica) y RA(Recuperación Académica)</h2>
            <div className="card">
              <div className="card-body">
                <form onSubmit={this.calcularPPS.bind(this)}>
                  <div className="form-group">
                    <label htmlFor="CC" className="text-secondary">Créditos Totales Cursados (o Créditos intentados) (CC)</label>
                    <input
                      className="form-control"
                      type="number"
                      step=".01"
                      name="CC"
                      placeholder="Digite los créditos Totales Cursados (o Créditos intentados)"
                        ref={this.CCRef}
                        required="required"
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="PC" className="text-secondary">Puntos de calidad para PGA totales (PC)</label>
                    <input
                      className="form-control"
                      type="number"
                      step=".01"
                      name="PC"
                      placeholder="Digita los puntos de calidad para PGA totales"
                      ref={this.PCRef}
                      required="required"
                    />
                  </div>

                  <div className="form-group">
                    <label htmlFor="CS" className="text-secondary">Créditos Matriculados en el Semestre (CS)</label>
                    <input
                      className="form-control"
                      type="number"
                      step=".01"
                      name="CS"
                      placeholder="Digita los créditos Matriculados en el Semestre"
                      ref={this.CSRef}
                      required="required"
                    />
                  </div>


                  <div className="form-group text-center">
                    <input type="submit" value="Calcular PPS" className="btn btn-success mr-2" />
                    <a href="/calculadora3ernota/" className="btn btn-info">Reiniciar</a>
                  </div>
                </form>
              </div>
            </div>
            <div className="mt-2">
              {this.mostrarResultado()}
            </div>
            </div>
    );
  }
}

export default CalculaPPS;
