import React from 'react';
import logo from './logo.png';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import CalculaPPP from './CalculaPPP';
import CalculaPPS from './CalculaPPS';
import 'react-tabs/style/react-tabs.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {nota3: 0,tabIndex: 0}
    //this.state = { }; //Para los tabs
  }
  
  /* Variables para la parte de las notas */
  nota1Ref = React.createRef();
  nota2Ref = React.createRef(); 
   /* Fin Variables para la parte de las notas */

 
  /* Metodo para el calculo de la nota */
  calcularNota(e) {
    e.preventDefault();    

    let nota1 = this.nota1Ref.current.value;
    let nota2 = this.nota2Ref.current.value;

    this.setState({
      nota3: ( 3 - ( (nota1 * .30) + (nota2 * .35) ) ) / 0.35
    });

    
    return this.state.nota3;
  }

  mostrarResultado = () => {

    if (this.state.nota3 === 0) return null;

    return (
      <h3 className="text-center alert alert-info">
        La nota mínima requerida para aprobar la asignatura es:<br/><h2><strong className="">{this.state.nota3}</strong></h2>
      </h3>
    )
  }
  /* Fin Metodo para el calculo de la nota */
  
  render() {
    return (
      //Logo
      <div>
        <div className="container mt-5">
        <div className="row justify-content-center text-white">
          <div className="col-md-6">
      <img src={logo} alt="Logo" className="img-fluid mb-5"/>
      </div>
      </div>
      </div>

      <div className="container mt-5">
      <div className="row justify-content-center text-white">
        <div className="col-md-9">
      <Tabs forceRenderTabPanel='true' selectedIndex={this.state.tabIndex} onSelect={tabIndex => this.setState({ tabIndex })}>
        <TabList>
          <Tab>Calculadora de 3 Nota</Tab>
          <Tab>Calculadora de PPS</Tab>
          <Tab>Calculadora de Promedio Ponderado Periodo</Tab>
        </TabList>
        <TabPanel>
            {/*
            <h1 className="text-center">Calculadora</h1>
            */}
            <h2 className="text-center">¿Cuánto debo obtener en el 3er corte para aprobar mi asignatura?</h2>
            <div className="card">
              <div className="card-body">
                <form onSubmit={this.calcularNota.bind(this)}>
                  <div className="form-group">
                    <label htmlFor="nota1" className="text-secondary">Nota 1er Corte</label>
                    <input
                      className="form-control"
                      type="number"
                      step=".01"
                      name="nota1"
                      placeholder="Digita la nota del primer corte"
                        ref={this.nota1Ref}
                        required="required"
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="nota2" className="text-secondary">Nota 2do Corte</label>
                    <input
                      className="form-control"
                      type="number"
                      step=".01"
                      name="nota1"
                      placeholder="Digita la nota del segundo corte"
                      ref={this.nota2Ref}
                      required="required"
                    />
                  </div>
                  <div className="form-group text-center">
                    <input type="submit" value="Calcular Nota" className="btn btn-success mr-2" />
                    <a href="/calculadora3ernota/" className="btn btn-info">Reiniciar</a>
                  </div>
                </form>
              </div>
            </div>
            
            <div className="mt-2">
              {this.mostrarResultado()}
            </div>
            
        </TabPanel>
        <TabPanel>
            <CalculaPPS/>
        </TabPanel>

        <TabPanel>              
            <CalculaPPP/>
        </TabPanel>
      </Tabs>
      </div>
      </div>   
            </div>
            </div>  
    );
  }
}



export default App;
