import React from 'react';
import logo from './logo.png';
import Table from './Table';

class CalculaPPP extends React.Component {
  constructor(props) {
    super(props);
    this.state = {materia:0,nota1:0,nota2:0,nota3:0,creditos:0,key:0,items:[]}
  }
  
  /* Variables para la parte del calculo promedio ponderado del periodo */
 MateriaRef = React.createRef();
 NotaCorte1Ref = React.createRef();
 NotaCorte2Ref = React.createRef();
 NotaCorte3Ref = React.createRef();
 CreditosRef = React.createRef();
  /* Fin Variables para la parte del calculo promedio ponderado del periodo */


  /* Metodo para el calculo promedio ponderado del periodo*/
  
  calcularPPP(e) {
    e.preventDefault();    
    let key = this.state.key;
    let Materia = this.MateriaRef.current.value;
    let NotaCorte1 = parseFloat(this.NotaCorte1Ref.current.value);
    let NotaCorte2 = parseFloat(this.NotaCorte2Ref.current.value);
    let NotaCorte3 = parseFloat(this.NotaCorte3Ref.current.value);
    let Creditos = parseInt(this.CreditosRef.current.value);
    
    let items = [...this.state.items];

    items.push({
      key:key,
      materia: Materia, 
      nota1: NotaCorte1,
      nota1p: NotaCorte1*0.30,
      nota2: NotaCorte2,
      nota2p: NotaCorte2*0.35,
      nota3: NotaCorte3,
      nota3p: NotaCorte3*0.35,
      creditos: Creditos,
      nota_curso: NotaCorte1*0.30+NotaCorte2*0.35+NotaCorte3*0.35,
      nota_creditos: Creditos*(NotaCorte1*0.30+NotaCorte2*0.35+NotaCorte3*0.35)
    });

    this.setState({
      items,
      key:this.state.key+1,
      materia: '', 
      nota1: 0,
      nota1p: 0,
      nota2: 0,
      nota2p: 0,
      nota3: 0,
      nota3p: 0,
      creditos: 0,
      nota_curso:0,
      nota_creditos:0
    });
  
    this.state.key += 1;
    e.target.reset();
    return this.state.items;
  }

  mostrarResultadoPPP = () => {

    if (this.state.items.length === 0) return null;
    const items = this.state.items;
    
    return (
      <div>
      <h3 className="text-center alert alert-info">
        El promedio ponderado del semestre (PPP) es el siguiente:<br/><h2><strong className="">{this.state.materia}</strong></h2>
      </h3>
      
      
      <Table items={ this.state.items }/>

      </div>
    )
  }
   
  /* Fin Metodo para el calculo promedio ponderado del periodo */


  render() {
    return (
      <div>
      <h2 className="text-center">¿Cuánto es mi promedio ponderado del periodo?</h2>
      <div className="card">
        <div className="card-body">
          <form onSubmit={this.calcularPPP.bind(this)}>
            <div className="form-group">
              <label htmlFor="Materia" className="text-secondary">Nombre del curso</label>
              <input
                className="form-control"
                type="text"
                step=".01"
                name="Materia"
                placeholder="Digite el nombre del curso"
                  ref={this.MateriaRef}
                  required="required"
              />
            </div>
            <div className="form-group">
              <label htmlFor="NotaCorte1" className="text-secondary">Nota 1er Corte</label>
              <input
                className="form-control"
                type="number"
                step=".01"
                name="NotaCorte1"
                placeholder="Digita la nota del primer corte"
                ref={this.NotaCorte1Ref}
                required="required"
              />
            </div>

            <div className="form-group">
            <label htmlFor="NotaCorte2" className="text-secondary">Nota 2do Corte</label>
              <input
                className="form-control"
                type="number"
                step=".01"
                name="NotaCorte2"
                placeholder="Digita la nota del segundo corte"
                ref={this.NotaCorte2Ref}
                required="required"
              />
            </div>

            <div className="form-group">
            <label htmlFor="NotaCorte3" className="text-secondary">Nota 3er Corte</label>
              <input
                className="form-control"
                type="number"
                step=".01"
                name="NotaCorte3"
                placeholder="Digita la nota del tercer corte"
                ref={this.NotaCorte3Ref}
                required="required"
              />
            </div>

            <div className="form-group">
            <label htmlFor="Creditos" className="text-secondary">Créditos</label>
              <input
                className="form-control"
                type="number"
                step=".01"
                name="Creditos"
                placeholder="Digita los créditos del curso"
                ref={this.CreditosRef}
                required="required"
              />
            </div>


            <div className="form-group text-center">
              <input type="submit" value="Calcular PPP" className="btn btn-success mr-2" />
              <a href="/calculadora3ernota/" className="btn btn-info">Reiniciar</a>
            </div>
          </form>
        </div>
      </div>
      <div className="mt-2">
        {this.mostrarResultadoPPP()}
      </div>
      </div>
    );
  }
}

export default CalculaPPP;
